<template>
  <div class="AccueilContact" @mouseover="animLettre">
    <header>
      <h2>
        <p data-animation="rubberBand">C</p>
        <p data-animation="rubberBand">o</p>
        <p data-animation="rubberBand">n</p>
        <p data-animation="rubberBand">t</p>
        <p data-animation="rubberBand">a</p>
        <p data-animation="rubberBand">c</p>
        <p data-animation="rubberBand">t</p>
      </h2>
    </header>
    <form ref="form" @submit.prevent="sendEmail">
      <Inputs :key="key">
        <div class="item">
          <input
            type="text"
            name="from_name"
            v-model="nom"
            ref="nom"
            id="nom"
            placeholder="Nom"
            :class="v$.nom.$error === true ? 'champs-error' : 'champs'"
          />

          <span v-for="error of v$.nom.$errors" :key="error.$uid">
            {{ error.$message }}
          </span>
        </div>
      </Inputs>
      <div class="item">
        <input
          type="email"
          name="from_email"
          ref="mail"
          id="mail"
          v-model="mail"
          placeholder="Mail"
          :class="v$.mail.$error === true ? 'champs-error' : 'champs'"
        />

        <span v-for="error of v$.mail.$errors" :key="error.$uid">
          {{ error.$message }}
        </span>
      </div>
      <div class="item">
        <textarea
          name="message"
          ref="message"
          id="message"
          v-model="message"
          placeholder="Message"
          :class="v$.message.$error === true ? 'champs-error' : 'champs'"
        ></textarea>

        <span v-for="error of v$.message.$errors" :key="error.$uid">
          {{ error.$message }}
        </span>
        <input type="submit" value="Envoyer" />
      </div>
    </form>
    <div class="notif" v-if="isNotif">
      <p>Votre message a bien été envoyé !</p>
    </div>
  </div>
</template>

<script>
import emailjs from "@emailjs/browser";
import useVuelidate from "@vuelidate/core";
import Inputs from "@/components/Accueil/Contact/Inputs";
import {
  required,
  email,
  minLength,
  maxLength,
  helpers,
} from "@vuelidate/validators";
export default {
  name: "AccueilContact",
  data() {
    return {
      v$: useVuelidate(),
      nom: null,
      mail: null,
      message: null,
      isNotif: false,
      key: 0,
    };
  },
  components: {
    Inputs,
  },
  validations() {
    return {
      nom: {
        required: helpers.withMessage(
          "Veuillez renseigner votre nom !",
          required
        ),
        $autoDirty: true,
      },
      mail: {
        required: helpers.withMessage(
          "Veuillez renseigner votre mail !",
          required
        ),
        email: helpers.withMessage("Votre mail n'est pas valide !", email),
        $autoDirty: true,
      },
      message: {
        required: helpers.withMessage("Veuillez écrire un message !", required),
        maxLength: helpers.withMessage(
          "Votre message ne peut pas dépasser 255 caractères !",
          maxLength(255)
        ),
        $autoDirty: true,
      },
    };
  },
  methods: {
    sendEmail() {
      this.v$.$touch();
      this.isNotif = false;
      if (
        this.$refs.nom.classList.value == "champs-error" ||
        this.$refs.mail.classList.value == "champs-error" ||
        this.$refs.message.classList.value == "champs-error" ||
        this.nom == null ||
        this.mail == null ||
        this.message == null
      ) {
        this.isNotif = false;
        console.log("error form contact !");
      } else {
        emailjs
          .sendForm(
            "service_ctp2ggb",
            "template_3oe9zsi",
            this.$refs.form,
            "HcUf1mpZ39vSUQ9RV"
          )
          .then(
            (result) => {
              console.log("SUCCESS!", result.text);
            },
            (error) => {
              console.log("FAILED...", error.text);
            }
          );
        console.log("form contact sent !");
        this.isNotif = true;
        this.key++;
      }
    },
  },
};
</script>

<style scoped>
.AccueilContact {
  z-index: 3;
  margin-top: 150px;
  margin-left: 90px;
  position: relative;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 80%;
}
.champs {
  margin-top: 1rem;
  width: 50rem;
  border: none;
  border-radius: 5px 5px 5px 5px;
  padding: 1rem;
  outline: none;
  background-color: #2b2b2b;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Lato";
}
@keyframes ok {
  to {
    border: 1px solid green;
  }
}
.champs-error {
  margin-top: 1rem;
  width: 50rem;
  border: 1px solid rgb(226, 39, 39);
  border-radius: 5px 5px 5px 5px;
  padding: 1rem;
  outline: none;
  background-color: #2b2b2b;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Lato";
}
textarea {
  resize: vertical;
  min-height: 10rem;
}
.item {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
input[type="submit"] {
  padding: 1rem;
  width: 15rem;
  height: 4rem;
  background: none;
  outline: none;
  border: 1px solid rgb(8, 253, 216);
  color: rgb(8, 253, 216);
  font-family: "Lato";
  letter-spacing: 1ch;
  margin-top: 1rem;
  margin-left: 37rem;
  cursor: pointer;
  transition: ease-out 0.4s;
  text-transform: uppercase;
  border-radius: 5px 5px 5px 5px;
}
span {
  padding: 1rem;
  color: rgb(226, 39, 39);
  font-family: "Lato";
}

input[type="submit"]:hover {
  box-shadow: inset 15rem 0 0 0 rgb(8, 253, 216);
  color: black;
}
.champs:-webkit-autofill {
  -webkit-text-fill-color: rgba(255, 255, 255, 0.5);
  -webkit-box-shadow: 0 0 0px 1000px rgba(0, 0, 0, 0) inset;
  transition: background-color 5000s ease-in-out 0s;
}
.notif {
  width: 15rem;
  background-color: rgb(8, 253, 216);
  color: black;
  padding: 1rem;
  font-family: "Lato";
  border-radius: 5px 5px 5px 5px;
  position: fixed;
  bottom: 2rem;
  right: -20rem;
  animation: displayNotif 5s;
  animation-fill-mode: forwards;
}
@keyframes displayNotif {
  10% {
    right: 2rem;
  }
  90% {
    right: 2rem;
  }
}
h2 {
  font-size: 3rem;
  font-family: "Poppins";
  font-weight: bold;
  color: rgb(8, 253, 216);
}
h2 p {
  display: inline-block;
  margin-block-start: 0;
  margin-block-end: 0;
}
h2::before {
  margin-top: -10px;
  left: -1rem;
  content: "<h2>";
  position: absolute;
  user-select: none;
  font-family: "La belle aurore";
  font-weight: lighter;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.2);
}
h2::after {
  content: "</h2>";
  position: absolute;
  user-select: none;
  font-family: "La belle aurore";
  font-weight: lighter;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.2);
  left: -1rem;
  margin-top: 60px;
}
.rubberBand {
  animation: rubberBand 1s;
}
@keyframes rubberBand {
  0% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(1.25, 0.75, 1);
  }
  45% {
    transform: scale3d(0.75, 1, 1);
  }
  60% {
    transform: scale3d(1.2, 0.8, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@media (max-width: 999px) {
  .champs {
    width: 30rem;
  }
  .champs-error {
    width: 30rem;
  }
  input[type="submit"] {
    margin-left: 17rem;
  }
}
@media (max-width: 640px) {
  .champs {
    width: 20rem;
  }
  .champs-error {
    width: 20rem;
  }
  input[type="submit"] {
    margin-left: 7rem;
  }
}
@media (max-width: 475px) {
  .champs {
    width: 12rem;
  }
  .champs-error {
    width: 12rem;
  }
  input[type="submit"] {
    margin-left: 0rem;
    width: 14rem;
  }
}
</style>